import styled from "styled-components";

export const All = styled.footer`
    background: transparent linear-gradient(248deg, #22274E 0%, #414776 100%) 0% 0%;
    width: 100%;
    color: #FFF;
    @media (max-width: 1024px) {
        display: grid;
    }
`;


export const Drop = styled.div`
    @media (min-width: 1025px) {
        display: none;
    }
`;